<template>
  <li @click="setNumber" class="numbers__item">
    <span v-if="!arabicNumbers">{{ number.indianNumber }}</span>
    <span v-else>{{ number.arabicNumber }}</span>
  </li>
</template>
<script>
import { mapActions } from "vuex";
export default {
  props: ["number", "arabicNumbers"],
  methods: {
    ...mapActions(["setNumber"]),
  },
};
</script>
