<template>
  <div @click="keepFocus" class="keyboard">
    <ul class="numbers">
      <arabic-numbers
        :key="number.arabicNumber"
        v-for="number in numbers"
        :number="number"
        :arabicNumbers="arabicNumbers"
      ></arabic-numbers>
      <li>
        <button
          style="font-size: 1rem"
          class="change-numbers-lang"
          @click="changeNumbersLanguage"
          type="button"
        >
          <span>{{ !arabicNumbers ? "الأحرف العربية" : "الأحرف الهندية" }}</span
          ><span>{{
            !arabicNumbers ? "Arabic numbers" : "Indian numbers"
          }}</span>
        </button>
      </li>
    </ul>
    <ul class="letters">
      <button @click="deleteNumber" class="change-numbers-lang" type="button">
        Delete
      </button>
      <arabic-letters
        :letter="letter"
        :key="letter"
        v-for="letter in theletters"
      ></arabic-letters>
    </ul>
    <div class="sub-menu">
      <a
        target="_blank"
        style="color: yellow"
        class="change-numbers-lang"
        :href="path"
      >
        Google </a
      ><button
        style="font-size: 2rem"
        @click="space"
        class="change-numbers-lang"
        type="button"
      >
        Espace
      </button>
      <a
        :href="pathYoutube"
        target="_blank"
        style="color: pink"
        class="change-numbers-lang"
      >
        Youtube
      </a>
    </div>
  </div>
</template>

<script>
import { mapActions } from "vuex";
import { mapGetters } from "vuex";

import ArabicNumbers from "./ArabicNumbers.vue";
import ArabicLetters from "./ArabicLetters.vue";
export default {
  components: { ArabicNumbers, ArabicLetters },
  data() {
    return {
      arabicNumbers: false,
      keys: [],
      numbers: [
        { arabicNumber: "1", indianNumber: "١" },
        { arabicNumber: "2", indianNumber: "٢" },
        { arabicNumber: "3", indianNumber: "٣" },
        { arabicNumber: "4", indianNumber: "٤" },
        { arabicNumber: "5", indianNumber: "٥" },
        { arabicNumber: "6", indianNumber: "٦" },
        { arabicNumber: "7", indianNumber: "٧" },
        { arabicNumber: "8", indianNumber: "٨" },
        { arabicNumber: "9", indianNumber: "٩" },
        { arabicNumber: "10", indianNumber: "٠" },
      ],
      letters: [
        "ا",
        "ب",
        "ت",
        "ث",
        "ج",
        "ح",
        "خ",
        "د",
        "ذ",
        "ر",
        "ز",
        "س",
        "ش",
        "ص",
        "ض",
        "ط",
        "ظ",
        "ع",
        "غ",
        "ف",
        "ق",
        "ك",
        "ل",
        "م",
        "ن",
        "ه",
        "و",
        "ي",
        "ء",
        "أ",
        "إ",
        "آ",
        "ة",
        "ؤ",
        "ئ",
        "ى",
        "ـ",
        ".",
        "،",
        "؛",
        "؟",
        ",ْ",
        " ّ",
        " ٌ",
        " ٍ",
        " ً",
        " ُ",
        " ِ",
        " َ",
      ],
    };
  },
  methods: {
    changeNumbersLanguage() {
      this.arabicNumbers = !this.arabicNumbers;
    },
    keepFocus(e) {
      console.log(e.target);
      document.querySelector("html").querySelector(".display").focus();
    },

    ...mapActions(["deleteNumber", "space"]),
  },
  computed: {
    theletters() {
      const myLetters = this.letters;
      //   myLetters.reverse();
      return myLetters;
    },
    ...mapGetters(["getNumber"]),
    path() {
      if (this.getNumber.trim() === "") return;
      return `https://www.google.com/search?q=${this.getNumber}`;
    },
    pathYoutube() {
      if (this.getNumber.trim() === "") return;
      return `https://www.youtube.com/results?search_query=${this.getNumber}`;
    },
  },
};
</script>

