<template>
  <the-clavier>
    <h1>Arabic keyboard</h1>
    <textarea
      :style="getincreaseFontSize"
      autocomplete="on"
      autocorrect="on"
      autofocus="on"
      dir="rtl"
      ref="val"
      :value="myText"
      @input="myInput"
      class="display"
    ></textarea>
    <the-menu></the-menu>
    <the-keyboard></the-keyboard>
    <the-how></the-how>
  </the-clavier>
</template>
<script>
// import { mapGetters } from "vuex";
// import { mapActions } from "vuex";
// import TheKey from "../components/TheKey.vue";
import TheClavier from "../components/clavier/TheClavier.vue";
import TheMenu from "../components/clavier/TheMenu.vue";
import TheKeyboard from "../components/clavier/TheKeyboard.vue";
import TheHow from "../components/TheHow.vue";
export default {
  data() {
    return {
      myNumber: "",
    };
  },
  components: { TheKeyboard, TheMenu, TheClavier, TheHow },
  computed: {
    myText() {
      return this.$store.getters["getNumber"];
    },
    getincreaseFontSize() {
      return `font-size:${this.$store.getters["getincreaseFontSize"]}rem`;
    },
  },
  methods: {
    myInput() {
      this.$store.dispatch("setArray", this.$refs.val.value.split(""));
    },
  },
};
</script>

